.dark-mode {
    background-color: darken($color: $grisOscuro, $amount: 5);

    p {
        color: $blanco;
    }

    a,
    h1,
    h2,
    h3,
    h4,
    blockquote {
        color: $gris;
    }

    .anuncio,
    .resumen-propiedad {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;

        .precio {
            color: $verde;
        }

        .iconos-caracteristicas img {
            filter: invert(100%);
        }
    }

    .form {

        label,
        legend,
        p {
            color: $gris;
        }

        input:not([type="submit"]),
        textarea,
        select {
            background-color: $grisOscuro;
            color: $blanco;
            border: 1px solid $grisOscuro;
        }
    }
}